import React from 'react';
import Link from 'gatsby-link';
import visacompanyImgBanner from "./static/images/visacompanyImgBanner.png";
import visacompanyLogo1 from "./static/images/visacompanyLogo1.jpg";
import visacompanyLogo2 from "./static/images/visacompanyLogo2.jpg";
import visacompanyLogo3 from "./static/images/visacompanyLogo3.jpg";
import visacompanyVisitingCard1 from "./static/images/visacompanyVisitingCard1.jpg";
import visacompanyVisitingCard2 from "./static/images/visacompanyVisitingCard2.jpg";
import visacompanyCup from "./static/images/visacompanyCup.jpg";
import boscoImg from "./static/images/boscoImg.jpg";
import supersonicImg from "./static/images/supersonicImg.jpg";

const visacompany = () => (
  <div className="fullBox">

    <div className="insideBanner visaCompanyBanner">
      <div className="bg visaCompany"></div>
      <div className="midWrapper">
            <div className="rightSection">
              <div className="box">
                <div className="vMid">
                  <img src={visacompanyImgBanner} alt="" />
                </div>
              </div>
            </div>
            <div className="leftSection">
              <div className="box">
                <div className="vMid">
                  <h1>VISA COMPANY <br />LOGO DESIGN</h1>
                  <p>Client : The Visa Company<br />Work : Logo Design | Freelance</p>
                </div>
              </div>
            </div>
      </div>
    </div>

    <div id="insideContent" className="visaCompanyContent">
      <div className="midWrapper">
        <div className="row">
          <ul className="three">
            <li><img src={visacompanyLogo1} alt="" />  </li>
            <li><img src={visacompanyLogo2} alt="" />  </li>
            <li><img src={visacompanyLogo3} alt="" />  </li>
          </ul>
        </div>
        <div className="row textAlignCenter">
          <img src={visacompanyVisitingCard1} alt="" />
        </div>
        <div className="row textAlignCenter">
          <img src={visacompanyVisitingCard2} alt="" />
        </div>
        <div className="row textAlignCenter">
          <img src={visacompanyCup} alt="" />
        </div>
      </div>
    </div>

    <div className="midWrapper nextPrevious textAlignCenter">
      <ul>
        <li className="back">
          <Link to="/boscojack">
            <div className="thumb"> <img src={boscoImg} alt="" /> </div>
            <div className="button"><span>Back</span></div>
          </Link>
        </li>
        <li className="next">
          <Link to="/vh1supersonic">
            <div className="thumb"> <img src={supersonicImg} alt="" /> </div>
            <div className="button"><span>next</span></div>
          </Link>
        </li>
      </ul>
    </div>

  </div>
)

export default visacompany



